import axios from 'axios';

const PasswordResetService = {
  changePassword(json) {
    const url = `${process.env.REACT_APP_HOST_URL}auth/ressetPassword`;
    return axios.post(url, json);
  },

  validateResetPasswordToken(requestId) {
    const url = `${process.env.REACT_APP_HOST_URL}auth/validateResetPasswordToken?requestId=${requestId}`;
    return axios.get(url);
  },

  async getLocation() {
    const res = await axios.get('https://ipapi.co/json');

    return res === undefined ? res : res.data;
  },
};

export default PasswordResetService;
