import React, { useState, useEffect } from 'react';
import { useParams, NavLink } from 'react-router-dom';
import PasswordStrengthBar from 'react-password-strength-bar';
import { browserName } from 'react-device-detect';
import Styles from './PasswordReset.module.css';
import './password-strength-meter.css';
import op2miseLogo from '../../assets/icons/Op2mizev0.6-for-light-bg.svg';
import eyeIcon from '../../assets/icons/Eye.png';
import eyeOnIcon from '../../assets/icons/Eye-On.png';
import PasswordResetService from './PasswordResetService';
import FooterComponent from '../../shared/footer/footer.component';

function PasswordResetComponent() {
  const { urlParamId } = useParams();

  const [showPassword, setShowPassword] = useState('password');
  const [showConfirmPassword, setShowConfirmPassword] = useState('password');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState();
  const [isPasswordChanged, setIsPasswordChanged] = useState(false);
  const [isTokenExpired, setIsTokenExpired] = useState(false);
  const [isNewAccount, setIsNewAccount] = useState(false);
  const [isChangePasswordClicked, setIsChangePasswordClicked] = useState(false);
  const [showErrorAlert, setShowErrorAlert] = useState(false);
  const [errorAlertMessage, setErrorAlertMessage] = useState('');
  const [passwordStrengthMeter, setPasswordStrengthMeter] = useState();
  const [passwordStrengthText, setPasswordStrengthText] = useState('too short');
  const [isLoading, setIsLoading] = useState(true);
  const [location, setLocation] = useState('');
  const [isGettingLocation, setIsGettingLocation] = useState(false);
  const [executeChangePassword, setExecuteChangePassword] = useState(false);

  const togglePassword = (fieldName) => {
    if (fieldName === 'password') {
      if (showPassword === 'password') {
        setShowPassword('text');
      } else {
        setShowPassword('password');
      }
    } else if (showConfirmPassword === 'password') {
      setShowConfirmPassword('text');
    } else {
      setShowConfirmPassword('password');
    }
  };

  const handleChangePassword = () => {
    if (confirmPassword === password) {
      setShowErrorAlert(false);
      setIsChangePasswordClicked(true);
      PasswordResetService.changePassword({
        resetPasswordId: urlParamId,
        newPassword: password,
        location,
        device: browserName,
      })
        .then(() => {
          setIsPasswordChanged(true);
        })
        .finally(() => {
          setIsChangePasswordClicked(false);
          setExecuteChangePassword(false);
        });
    } else {
      setExecuteChangePassword(false);
      setShowErrorAlert(true);
      setErrorAlertMessage(
        'New password and Confirm new password does not match'
      );
    }
  };

  const getLocation = () => {
    setIsGettingLocation(true);
    PasswordResetService.getLocation()
      .then((data) => {
        const address = data ? `${data.city}, ${data.country_name}` : 'Unknown';
        setLocation(address);
        setExecuteChangePassword(true);
      })
      .finally(() => setIsGettingLocation(false));
  };

  const onChangePassword = () => {
    if (password.length === 0) {
      setPasswordStrengthMeter();
      setPasswordStrengthText('too short');
    }
    if (password.length >= 8) {
      setPasswordStrengthMeter(1);
      setPasswordStrengthText('weak');
    }
    if (password.length >= 8 && /\d/.test(password)) {
      setPasswordStrengthMeter(2);
      setPasswordStrengthText('okay');
    }
    if (password.length >= 8 && /\d/.test(password) && /[A-Z]/.test(password)) {
      setPasswordStrengthMeter(3);
      setPasswordStrengthText('good');
    }
    if (
      password.length >= 8 &&
      /\d/.test(password) &&
      /[A-Z]/.test(password) &&
      /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[-!@#$%^&*()_+={}[\]:;<>,.?~/\\`|'"]).{8,}$/.test(password)
    ) {
      setPasswordStrengthMeter(4);
      setPasswordStrengthText('strong');
    }
  };

  useEffect(() => {
    setIsLoading(true);
    PasswordResetService.validateResetPasswordToken(urlParamId)
      .then((response) => {
        setIsTokenExpired(response.data.isExpired);
        setIsNewAccount(response.data.isNewAccount);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }, [urlParamId]);

  useEffect(() => {
    if (executeChangePassword) handleChangePassword();
  }, [executeChangePassword]);

  return (
    <div className={Styles.body}>
      {!isLoading && (
        <>
          <div className={Styles.panel}>
            <div className="container">
              <div className="row py-3 text-center">
                {isTokenExpired ? (
                  <>
                    <p className={Styles.title}>
                      <img
                        className={`${Styles.virtualAiLogo} my-4`}
                        src={op2miseLogo}
                        alt="op2miseLogo"
                      />
                      <br />
                      Invalid password reset link
                    </p>
                    <p className={Styles.description}>
                      The password reset link you followed is no longer valid.
                      This is probably because the link is already expired.
                    </p>
                    <NavLink
                      className="text-white text-decoration-none mb-5"
                      to={`${process.env.REACT_APP_SUBFOLDER}/login`}
                    >
                      <button className={Styles.button} type="button">
                        Back to sign in
                      </button>
                    </NavLink>
                  </>
                ) : (
                  <div>
                    {isPasswordChanged ? (
                      <>
                        <p className={Styles.title}>
                          <img
                            className={`${Styles.virtualAiLogo} my-4`}
                            src={op2miseLogo}
                            alt="op2miseLogo"
                          />
                          <br />
                          {isNewAccount
                            ? 'New password successfully set!'
                            : 'Password successfully changed!'}
                        </p>
                        <p className={Styles.description}>
                          You can now use your new password to sign in to your
                          account.
                        </p>
                        <NavLink
                          className="text-white text-decoration-none mt-4 mb-2"
                          to={`${process.env.REACT_APP_SUBFOLDER}/login`}
                        >
                          <button className={Styles.button} type="button">
                            Sign In
                          </button>
                        </NavLink>
                      </>
                    ) : (
                      <>
                        <p className={Styles.title}>
                          <img
                            className={`${Styles.virtualAiLogo} my-4`}
                            src={op2miseLogo}
                            alt="op2miseLogo"
                          />
                          <br />
                          Set your new password
                        </p>
                        <p className={Styles.description}>
                          Your password needs to contain at least 8 characters
                          in length including at least 1 number, 1 special
                          character, 1 uppercase letter and 1 lowercase letter.
                        </p>
                        <div className="input-group position-relative">
                          <input
                            className={Styles.passwordInput}
                            type={showPassword}
                            placeholder="New password"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                          />
                          <div
                            className={`${Styles.eyeIcon} input-group-append`}
                          >
                            {showPassword === 'password' ? (
                              <img
                                style={{ cursor: 'pointer' }}
                                src={eyeIcon}
                                alt="EyeIcon"
                                onClick={() => togglePassword('password')}
                                onKeyDown={() => togglePassword('password')}
                                role="presentation"
                              />
                            ) : (
                              <img
                                style={{
                                  cursor: 'pointer',
                                  height: '12.03px',
                                  width: '15.03px',
                                }}
                                src={eyeOnIcon}
                                alt="EyeOnIcon"
                                onClick={() => togglePassword('password')}
                                onKeyDown={() => togglePassword('password')}
                                role="presentation"
                              />
                            )}
                          </div>
                        </div>
                        <div className={Styles.passworthStrengthBarDiv}>
                          <PasswordStrengthBar
                            className={`${passwordStrengthMeter === 1 &&
                              'password-reset-password-meter-strength-1'} ${passwordStrengthMeter ===
                              2 &&
                              'password-reset-password-meter-strength-2'} ${passwordStrengthMeter ===
                              3 &&
                              'password-reset-password-meter-strength-3'} ${passwordStrengthMeter ===
                              4 && 'password-reset-password-meter-strength-4'}`}
                            password={password}
                            barColors={[
                              '#ddd',
                              '#ff5571',
                              '#ffd083',
                              '#2178f1',
                              '#00d583',
                            ]}
                            scoreWords={[]}
                            shortScoreWord=""
                            onChangeScore={onChangePassword}
                          />
                        </div>
                        <div
                          className="input-group position-relative"
                          style={{
                            pointerEvents: password.length < 8 && 'none',
                          }}
                        >
                          <input
                            className={Styles.passwordInput}
                            type={showConfirmPassword}
                            placeholder="Confirm new password"
                            value={confirmPassword}
                            onChange={(e) => setConfirmPassword(e.target.value)}
                            disabled={password.length < 8}
                          />
                          <div
                            className={`${Styles.eyeIcon} input-group-append`}
                          >
                            {showConfirmPassword === 'password' ? (
                              <img
                                style={{ cursor: 'pointer' }}
                                src={eyeIcon}
                                alt="EyeIcon"
                                onClick={() =>
                                  togglePassword('confirmPassword')
                                }
                                onKeyDown={() =>
                                  togglePassword('confirmPassword')
                                }
                                role="presentation"
                              />
                            ) : (
                              <img
                                style={{
                                  cursor: 'pointer',
                                  height: '12.03px',
                                  width: '15.03px',
                                }}
                                src={eyeOnIcon}
                                alt="EyeOnIcon"
                                onClick={() =>
                                  togglePassword('confirmPassword')
                                }
                                onKeyDown={() =>
                                  togglePassword('confirmPassword')
                                }
                                role="presentation"
                              />
                            )}
                          </div>
                        </div>
                        {showErrorAlert && (
                          <p className={Styles.errorAlertMessage}>
                            {errorAlertMessage}
                          </p>
                        )}
                        <button
                          className={Styles.button}
                          style={{
                            pointerEvents:
                              (isChangePasswordClicked || isGettingLocation) &&
                              'none',
                            opacity:
                              (isChangePasswordClicked || isGettingLocation) &&
                              '0.8',
                            cursor:
                              (isChangePasswordClicked || isGettingLocation) &&
                              'default',
                            marginTop: showErrorAlert ? '0px' : '40px',
                            // marginLeft: '63px',
                          }}
                          onClick={getLocation}
                          disabled={passwordStrengthMeter !== 4}
                          type="button"
                        >
                          {isNewAccount
                            ? 'Set new password'
                            : 'Change Password'}
                          {(isChangePasswordClicked || isGettingLocation) && (
                            <i className="fas fa-circle-notch fa-spin mx-2" />
                          )}
                        </button>
                      </>
                    )}
                  </div>
                )}
              </div>
            </div>
          </div>
          <FooterComponent />
        </>
      )}
    </div>
  );
}

export default PasswordResetComponent;
